import React from 'react';
import Image from 'next/image';
import styles from '../styles/Projects.module.css';

export default function Projects() {
  const projects = [
    {
      title: 'E-Commerce Website',
      description: 'A full-featured online shopping platform with cart, checkout, and payment integration.',
      image: '/assets/images/project1.jpg',
      tech: ['React', 'Node.js', 'MongoDB', 'Stripe']
    },
    {
      title: 'Portfolio Website',
      description: 'Modern and responsive portfolio website showcasing projects and skills.',
      image: '/assets/images/project2.jpg',
      tech: ['Next.js', 'TypeScript', 'CSS3']
    },
    {
      title: 'Todo Application',
      description: 'Task management app with CRUD operations and local storage.',
      image: '/assets/images/project3.jpg',
      tech: ['React', 'JavaScript', 'CSS']
    },
    {
      title: 'Myntra Clone',
      description: 'E-commerce fashion store clone with product filtering and search.',
      image: '/assets/images/project4.jpg',
      tech: ['HTML', 'CSS', 'JavaScript']
    },
    {
      title: 'Game Project',
      description: 'Interactive browser-based game with animations and score tracking.',
      image: '/assets/images/project5.jpg',
      tech: ['JavaScript', 'Canvas', 'CSS3']
    },
    {
      title: 'Business Website',
      description: 'Corporate website with services, testimonials, and contact form.',
      image: '/assets/images/project6.jpg',
      tech: ['HTML', 'CSS', 'JavaScript', 'Bootstrap']
    }
  ];

  return (
    <section id="projects" className={styles.projectsSection}>
      <div className={styles.projectsContainer}>
        <div className={styles.projectsHeader}>
          <h2 className={styles.title}>
            My <span className={styles.highlight}>Projects</span>
          </h2>
          <p className={styles.subtitle}>Some of my recent work</p>
        </div>

        <div className={styles.projectsGrid}>
          {projects.map((project, index) => (
            <div key={index} className={styles.projectCard}>
              <div className={styles.projectImage}>
                <Image
                  src={project.image}
                  alt={project.title}
                  width={400}
                  height={300}
                  className={styles.cardImage}
                />
              </div>
              <div className={styles.projectInfo}>
                <h3 className={styles.projectTitle}>{project.title}</h3>
                <p className={styles.projectDescription}>{project.description}</p>
                <div className={styles.projectTech}>
                  {project.tech.map((tech, techIndex) => (
                    <span key={techIndex} className={styles.techBadge}>{tech}</span>
                  ))}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
